import PopupWithForm from "./PopupWithForm.js";
import React from "react";

export default function AddPlacePopup (props) {

    const [name,setName] = React.useState("");
    const [link,setLink] = React.useState("");
   
    function handleSubmit(e) {
        e.preventDefault();
        
        props.onAddPlace({
          name:name,
          link:link
        });
        setName("");
        setLink("");
      } 

      function clearForm() {
        props.onClose()
        setName("");
        setLink("");
      }


    return (
        <PopupWithForm
          title="ÐÐ¾Ð²Ð¾Ðµ Ð¼ÐµÑÑÐ¾"
          name="place"
          isOpen={props.isOpen}
          onClose={clearForm}
          buttonText={props.isLoading ? "ÐÐ¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ..." : "ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ"}
          submitType="disabled"
          onSubmit={handleSubmit}
        >
          <input
            name="name"
            type="text"
            placeholder="ÐÐ°Ð·Ð²Ð°Ð½Ð¸Ðµ"
            className="popup__input"
            id="place"
            minLength="2"
            maxLength="40"
            value={name || ""}
            required
            onChange={(e) => {setName( e.target.value)}}
          />
          <span id="place-error" className="form__error"></span>
          <input
            name="link"
            type="url"
            placeholder="Ð¡ÑÑÐ»ÐºÐ° Ð½Ð° ÐºÐ°ÑÑÐ¸Ð½ÐºÑ"
            className="popup__input"
            id="link"
            minLength="2"
            maxLength="200"
            value={link || ""}
            required
            onChange={(e) => {setLink( e.target.value)}}
          />
          <span id="link-error" className="form__error"></span>
        </PopupWithForm>
    )
}