import PopupWithForm from "./PopupWithForm.js";
import React from "react";

export default function EditAvatarPopup (props) {
    
    const avatarEdit = React.useRef();

    function handleSubmit(e) {
        e.preventDefault();
      
        props.onUpdateAvatar({
          avatar: avatarEdit.current.value
        });
        e.target.reset();
      } 

      function clearForm() {
        props.onClose()
        avatarEdit.current.value = "";
      }

    return (
        <PopupWithForm
          title="ÐÐ±Ð½Ð¾Ð²Ð¸ÑÑ Ð°Ð²Ð°ÑÐ°Ñ"
          name="avatar"
          isOpen={props.isOpen}
          onClose={clearForm}
          buttonText={props.isLoading ? "Ð¡Ð¾ÑÑÐ°Ð½ÐµÐ½Ð¸Ðµ..." : "Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ"}
          submitType="disabled"
          onSubmit={handleSubmit}
        >
          <input
            name="avatar"
            type="url"
            placeholder="Ð¡ÑÑÐ»ÐºÐ° Ð½Ð° ÐºÐ°ÑÑÐ¸Ð½ÐºÑ"
            className="popup__input"
            id="avatar"
            minLength="2"
            maxLength="200"
            required
            ref={avatarEdit}
          />
          <span id="avatar-error" className="form__error"></span>
        </PopupWithForm>
    )
}